import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Person } from 'src/app/common/dto/person';
import { Activity } from 'src/app/common/enums/activity.enum';
import { LaunchActivity } from 'src/app/common/interface/launch-activity';
import { StringKeyVal } from 'src/app/common/interface/string-key-val';
import { ActivityService } from 'src/app/common/service/activity/activity.service';
import { PersonService } from 'src/app/common/service/person/person.service';
import { TemplateService } from 'src/app/common/service/template/template.service';

@Component({
	selector: 'app-view',
	templateUrl: './view.component.html',
	styleUrls: ['./view.component.css']
})
export class ViewComponent implements OnInit {

	public personList: Person[];
	public activityType: typeof Activity = Activity;
	public selectedActivity: Activity = Activity.None;
	public templateList: StringKeyVal[];
	public selectedTemplate: string | undefined = undefined;
	public userCount: number = 0;

	constructor(private router: Router, private personService: PersonService,
		private activeRoute: ActivatedRoute, private templateService: TemplateService,
		private activityService: ActivityService) {
		const campaignId: string = this.activeRoute.snapshot.params.id;
		this.personList = this.personService.getUsers(campaignId);
		this.templateList = this.templateService.getDataset();
	}

	ngOnInit(): void {

	}

	navigateToHome(): void {
		this.router.navigateByUrl("/home")
	}

	changeSelection(activity: Activity): void {
		this.selectedActivity = activity;
	}

	navigateToTemplate(): void {
		this.router.navigateByUrl("/template/dashboard")
	}

	launchCampaign(): void {
		const launchActivity: LaunchActivity = {
			campaignId: this.activeRoute.snapshot.params.id,
			templateId: this.selectedTemplate,
			type: this.selectedActivity,
			count: this.userCount
		}
		this.activityService.lauchActivity(launchActivity);
	}
}
