import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Person } from 'src/app/common/dto/person';
import { CampaignService } from 'src/app/common/service/campaign/campaign.service';
import { PersonService } from 'src/app/common/service/person/person.service';
import { environment } from 'src/environments/environment';

@Component({
	selector: 'app-feature',
	templateUrl: './feature.component.html',
	styleUrls: ['./feature.component.css']
})
export class FeatureComponent implements OnInit {

	@Input("text")
	public text: string = "";

	@Input("location")
	public location: string = "/home";

	public peopleImgList: string[] = [];

	constructor(private router: Router, private activeRoute: ActivatedRoute, 
		private personService: PersonService, private campaignService: CampaignService) {

	}

	ngOnInit(): void {
		this.personService.getUsersByCount(environment.api.person.displayCount, this.location).then(
			(personList: Person[]) => {
				this.peopleImgList.splice(0);
				personList.forEach((person: Person)=> {
					this.peopleImgList.push(person.img);
				})
			}
		)
	}

	navigateToLocation(): void {
		this.router.navigate(['../view/' + this.location], { relativeTo: this.activeRoute });
	}

	removeCampaign(event: Event): void {
		this.campaignService.remove(this.location);
		event.stopPropagation();
	}

}
