import { Options } from '@angular-slider/ngx-slider';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Campaign } from 'src/app/common/dto/campaign';
import { CampaignService } from 'src/app/common/service/campaign/campaign.service';
import { PersonService } from 'src/app/common/service/person/person.service';

@Component({
	selector: 'app-prepare',
	templateUrl: './prepare.component.html',
	styleUrls: ['./prepare.component.css']
})
export class PrepareComponent implements OnInit {

	public value: number = 0;
	public options: Options = {
		ceil: 10,
		step: 1
	}

	public prepareForm: FormGroup;
	public sliderControl: FormControl;

	constructor(builder: FormBuilder, private campaignService: CampaignService, private toastr: ToastrService,
		private personService: PersonService, private router: Router, private activeRoute: ActivatedRoute) {

		this.sliderControl = new FormControl(0, [Validators.required, Validators.min(1), Validators.maxLength(2500)]);

		this.prepareForm = builder.group({
			url: ['', [Validators.required]],
			name: ['', [Validators.required]],
			loadCount: this.sliderControl
		});

	}

	ngOnInit(): void {

	}

	onSubmit(): void {
		this.prepareForm.markAllAsTouched();

		if (this.prepareForm.valid) {
			const campaign: Campaign = this.prepareForm.value;
			this.campaignService.create(campaign).then(
				(campaign: Campaign) => {
					this.toastr.success("Campaign was created successfully. Please allow us some time to load result");
					this.personService.fetchUsersProfiles(campaign);
					this.router.navigate(['../view/'+campaign.id], {
						relativeTo: this.activeRoute
					});
				},
				(reason: any) => {
					if (reason.message != undefined) {
						this.toastr.error("Could not create campaign due to '" + reason.message + "'");
					} else {
						this.toastr.error("Could not create campaign. Please try after some time.");
					}
				}
			);
		}
	}

}
